import React from 'react'
import { Link } from 'gatsby'

// cssモジュールは相対パスで指定してあげる
import './Header.module.css'

const Header = () => {
    return (
        <header>
            <nav>
                <ul>
                    <li><Link to="/">top</Link></li>
                    <li><Link to="/about">about</Link></li>
                </ul>
            </nav>
        </header>
    )
}

export default Header